"use client"

import { useState } from "react"
import Link from "next/link"
import { ChevronLeft, Clock, Award, FileText, Users, RefreshCw, Vote } from "lucide-react"
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from "@/components/ui/card"
import { Badge } from "@/components/ui/badge"
import { Separator } from "@/components/ui/separator"
import { Progress } from "@/components/ui/progress"
import { Button } from "@/components/ui/button"
import { Tooltip, TooltipContent, TooltipProvider, TooltipTrigger } from "@/components/ui/tooltip"
import { toast } from "sonner"
import { useContract } from "@/context/contract-context"
import { formatTimestamp, formatAddress } from "@/lib/utils"
import CountdownTimer from "./countdown-timer"
import UserStatusIndicator from "./user-status-indicator"

// Map enum numbers to strings
const VotingType = ["CandidateBased", "ProposalBased"]
const VotingRestriction = ["OpenToAll", "Limited", "RequiredRegistration"]
const ResultType = ["RankBased", "OneWinner"]
const CampaignStatus = ["Created", "Active", "Ended"]

const CampaignHeader = ({
  campaignInfo,
  timeRemaining,
  isBeforeStart,
  isDuringVoting,
  needsStatusUpdate,
  actionLoading,
  actionError,
  onUpdateStatus,
  campaignId,
}) => {
  const { contract, signer, isConnected } = useContract()
  const [updating, setUpdating] = useState(false)

  const handleUpdateStatus = async () => {
    if (!isConnected) {
      toast.error("Wallet not connected", {
        description: "Please connect your wallet to update status",
      })
      return
    }

    if (!contract || !signer || !campaignId) {
      toast.error("Cannot update status", {
        description: "Wallet not connected or contract not loaded",
      })
      return
    }

    setUpdating(true)
    try {
      const contractWithSigner = contract.connect(signer)
      const tx = await contractWithSigner.updateCampaignStatus(campaignId)

      toast.success("Status update submitted", {
        description: "Status update transaction has been sent to the blockchain",
      })

      await tx.wait()

      toast.success("Status updated", {
        description: "Campaign status has been updated",
      })

      if (onUpdateStatus) onUpdateStatus()
    } catch (err) {
      console.error("Status update error:", err)
      let errorMessage = "Failed to update status."
      if (err.reason) errorMessage = err.reason
      else if (err.data?.message) errorMessage = err.data.message
      else if (err.message) errorMessage = err.message

      toast.error("Status update failed", {
        description: errorMessage,
      })
    } finally {
      setUpdating(false)
    }
  }

  const getStatusBadgeClass = (status) => {
    switch (status) {
      case 0: // Created
        return "bg-purple-600 hover:bg-purple-700"
      case 1: // Active
        return "bg-green-600 hover:bg-green-700"
      case 2: // Ended
        return "bg-red-600 hover:bg-red-700"
      default:
        return "bg-gray-600 hover:bg-gray-700"
    }
  }

  return (
    <>
      <div className="mb-6">
        <Link
          href="/campaigns"
          className="flex items-center text-purple-300 hover:text-purple-100 transition-colors mb-4"
        >
          <ChevronLeft className="h-4 w-4 mr-1" />
          Back to campaigns
        </Link>
      </div>

      <Card className="border-purple-800 bg-purple-900 shadow-lg">
        <CardHeader>
          <div className="flex flex-col md:flex-row md:justify-between md:items-start gap-2">
            <div>
              <CardTitle className="text-2xl text-purple-100">{campaignInfo.description}</CardTitle>
              <CardDescription className="mt-2 text-purple-300">
                Created by: <span className="font-mono">{formatAddress(campaignInfo.creator)}</span>
              </CardDescription>
            </div>
            <Badge className={getStatusBadgeClass(campaignInfo.status)}>{CampaignStatus[campaignInfo.status]}</Badge>
          </div>
        </CardHeader>

        <CardContent className="space-y-6">
          <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
            <TooltipProvider>
              <Tooltip>
                <TooltipTrigger asChild>
                  <div className="flex items-center gap-2 bg-purple-800/50 p-3 rounded-lg">
                    <FileText className="h-5 w-5 text-purple-300" />
                    <div>
                      <p className="text-sm text-purple-300">Voting Type</p>
                      <p className="font-medium text-purple-100">{VotingType[campaignInfo.votingType]}</p>
                    </div>
                  </div>
                </TooltipTrigger>
                <TooltipContent>
                  <p>
                    {campaignInfo.votingType === 0
                      ? "Vote for candidates who have registered for this campaign"
                      : "Vote for proposals created for this campaign"}
                  </p>
                </TooltipContent>
              </Tooltip>
            </TooltipProvider>

            <TooltipProvider>
              <Tooltip>
                <TooltipTrigger asChild>
                  <div className="flex items-center gap-2 bg-purple-800/50 p-3 rounded-lg">
                    <Award className="h-5 w-5 text-purple-300" />
                    <div>
                      <p className="text-sm text-purple-300">Result Type</p>
                      <p className="font-medium text-purple-100">{ResultType[campaignInfo.resultType]}</p>
                    </div>
                  </div>
                </TooltipTrigger>
                <TooltipContent>
                  <p>
                    {campaignInfo.resultType === 0
                      ? "Results will be ranked by number of votes"
                      : "Only the option with the most votes will win"}
                  </p>
                </TooltipContent>
              </Tooltip>
            </TooltipProvider>

            <TooltipProvider>
              <Tooltip>
                <TooltipTrigger asChild>
                  <div className="flex items-center gap-2 bg-purple-800/50 p-3 rounded-lg">
                    <Clock className="h-5 w-5 text-purple-300" />
                    <div>
                      <p className="text-sm text-purple-300">Time Frame</p>
                      <p className="font-medium text-purple-100">
                        {formatTimestamp(campaignInfo.startTime)} - {formatTimestamp(campaignInfo.endTime)}
                      </p>
                    </div>
                  </div>
                </TooltipTrigger>
                <TooltipContent>
                  <p>The time period during which voting is active</p>
                </TooltipContent>
              </Tooltip>
            </TooltipProvider>

            <TooltipProvider>
              <Tooltip>
                <TooltipTrigger asChild>
                  <div className="flex items-center gap-2 bg-purple-800/50 p-3 rounded-lg">
                    <Users className="h-5 w-5 text-purple-300" />
                    <div>
                      <p className="text-sm text-purple-300">Access Restriction</p>
                      <p className="font-medium text-purple-100">
                        {VotingRestriction[campaignInfo.restriction]}
                        {campaignInfo.restriction === 1 &&
                          campaignInfo.maxVoters > 0n &&
                          ` (Max: ${campaignInfo.maxVoters.toString()})`}
                      </p>
                    </div>
                  </div>
                </TooltipTrigger>
                <TooltipContent>
                  <p>
                    {campaignInfo.restriction === 0
                      ? "Anyone can vote without registration"
                      : campaignInfo.restriction === 1
                        ? "Limited number of voters can register"
                        : "Registration with passkey required to vote"}
                  </p>
                </TooltipContent>
              </Tooltip>
            </TooltipProvider>
          </div>

          <Separator className="bg-purple-700" />

          <UserStatusIndicator campaignInfo={campaignInfo} />

          <div className="space-y-2">
            <div className="flex justify-between items-center">
              <div className="flex items-center gap-2">
                <Vote className="h-5 w-5 text-purple-300" />
                <span className="font-medium text-purple-100">Voting Progress</span>
              </div>
              <span className="text-sm text-purple-300">
                {campaignInfo.totalVotes.toString()} votes cast
                {campaignInfo.restriction !== 0 && ` / ${campaignInfo.registeredVoterCount.toString()} registered`}
              </span>
            </div>

            <Progress
              value={
                campaignInfo.registeredVoterCount > 0n
                  ? (Number(campaignInfo.totalVotes) / Number(campaignInfo.registeredVoterCount)) * 100
                  : 0
              }
              className="h-2 bg-purple-800"
              indicatorClassName="bg-purple-400"
            />
          </div>

          {/* Countdown Timer */}
          {(isBeforeStart || isDuringVoting) && (
            <CountdownTimer timeRemaining={timeRemaining} isBeforeStart={isBeforeStart} />
          )}

          {needsStatusUpdate && (
            <div className="flex justify-center">
              <Button
                onClick={handleUpdateStatus}
                disabled={updating}
                className="flex items-center gap-2 bg-purple-600 hover:bg-purple-700 text-white"
              >
                <RefreshCw className={`h-4 w-4 ${updating ? "animate-spin" : ""}`} />
                {updating ? "Updating Status..." : "Update Campaign Status"}
              </Button>
            </div>
          )}

          {actionError && (
            <div className="bg-red-900/50 border border-red-700 text-red-200 rounded-md p-3 text-sm">{actionError}</div>
          )}
        </CardContent>
      </Card>
    </>
  )
}

export default CampaignHeader

