"use client"

import { useState } from "react"
import { Tabs, TabsContent, TabsList, TabsTrigger } from "@/components/ui/tabs"
import { BarChart, UserPlus, Vote, Info } from "lucide-react"
import OverviewTab from "./tabs/overview-tab"
import ResultsTab from "./tabs/results-tab"
import VoteTab from "./tabs/vote-tab"
import RegisterTab from "./tabs/register-tab"

export default function CampaignTabs({
  campaignInfo,
  proposals,
  candidates,
  isConnected,
  userIsRegistered,
  userHasVoted,
  userIsCandidate,
  needsRegistration,
  isBeforeStart,
  isDuringVoting,
  isAfterEnd,
  canRegister,
  canVote,
  isOwner,
  address,
  campaignId,
  onUpdate,
}) {
  const [activeTab, setActiveTab] = useState("overview")

  // Hide register tab for registration-free campaigns
  const showRegisterTab = canRegister && !isOwner && needsRegistration

  return (
    <Tabs defaultValue="overview" className="w-full" onValueChange={setActiveTab} value={activeTab}>
      <TabsList className="grid grid-cols-2 md:grid-cols-4 mb-6 bg-white dark:bg-gray-900 border border-purple-100 dark:border-purple-900/50">
        <TabsTrigger
          value="overview"
          className="flex items-center gap-2 data-[state=active]:bg-purple-100 dark:data-[state=active]:bg-purple-900/30 data-[state=active]:text-purple-900 dark:data-[state=active]:text-purple-100"
        >
          <Info className="h-4 w-4" />
          <span>Overview</span>
        </TabsTrigger>

        <TabsTrigger
          value="results"
          className="flex items-center gap-2 data-[state=active]:bg-purple-100 dark:data-[state=active]:bg-purple-900/30 data-[state=active]:text-purple-900 dark:data-[state=active]:text-purple-100"
        >
          <BarChart className="h-4 w-4" />
          <span>Results</span>
        </TabsTrigger>

        {canVote && !isOwner && !userIsCandidate && (
          <TabsTrigger
            value="vote"
            className="flex items-center gap-2 data-[state=active]:bg-purple-100 dark:data-[state=active]:bg-purple-900/30 data-[state=active]:text-purple-900 dark:data-[state=active]:text-purple-100"
          >
            <Vote className="h-4 w-4" />
            <span>Vote</span>
          </TabsTrigger>
        )}

        {showRegisterTab && (
          <TabsTrigger
            value="register"
            className="flex items-center gap-2 data-[state=active]:bg-purple-100 dark:data-[state=active]:bg-purple-900/30 data-[state=active]:text-purple-900 dark:data-[state=active]:text-purple-100"
          >
            <UserPlus className="h-4 w-4" />
            <span>Register</span>
          </TabsTrigger>
        )}
      </TabsList>

      <TabsContent value="overview">
        <OverviewTab
          campaignInfo={campaignInfo}
          isOwner={isOwner}
          isBeforeStart={isBeforeStart}
          isDuringVoting={isDuringVoting}
          isAfterEnd={isAfterEnd}
          proposals={proposals}
          candidates={candidates}
        />
      </TabsContent>

      <TabsContent value="results">
        <ResultsTab campaignInfo={campaignInfo} proposals={proposals} candidates={candidates} isOwner={isOwner} />
      </TabsContent>

      {canVote && !isOwner && !userIsCandidate && (
        <TabsContent value="vote">
          <VoteTab
            campaignInfo={campaignInfo}
            proposals={proposals}
            candidates={candidates}
            isConnected={isConnected}
            userIsRegistered={userIsRegistered}
            userHasVoted={userHasVoted}
            needsRegistration={needsRegistration}
            address={address}
            onVote={onUpdate}
            campaignId={campaignId}
          />
        </TabsContent>
      )}

      {showRegisterTab && (
        <TabsContent value="register">
          <RegisterTab
            campaignInfo={campaignInfo}
            userIsRegistered={userIsRegistered}
            onRegister={onUpdate}
            campaignId={campaignId}
          />
        </TabsContent>
      )}
    </Tabs>
  )
}
