"use client"

import { useState } from "react"
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from "@/components/ui/card"
import { Button } from "@/components/ui/button"
import { Input } from "@/components/ui/input"
import { Textarea } from "@/components/ui/textarea"
import { Label } from "@/components/ui/label"
import { Tabs, TabsContent, TabsList, TabsTrigger } from "@/components/ui/tabs"
import { Alert, AlertDescription } from "@/components/ui/alert"
import { Check, UserPlus, User, Info, Upload, Sparkles } from "lucide-react"
import { useContract } from "@/context/contract-context"
import { toast } from "sonner"
import { handleRpcError } from "@/lib/error-handler"
import { FileUpload } from "@/components/ui/file-upload"

export default function RegisterTab({ campaignInfo, userIsRegistered, onRegister, campaignId }) {
  const { contract } = useContract()
  const [activeTab, setActiveTab] = useState("voter")
  const [voterPassKey, setVoterPassKey] = useState("")
  const [isSubmittingVoter, setIsSubmittingVoter] = useState(false)

  const [candidateForm, setCandidateForm] = useState({
    name: "",
    statement: "",
    imageHash: "",
    passKey: "",
  })
  const [isSubmittingCandidate, setIsSubmittingCandidate] = useState(false)
  const [selectedFile, setSelectedFile] = useState(null)
  const [isUploading, setIsUploading] = useState(false)
  const [registrationStep, setRegistrationStep] = useState(0) // 0: not started, 1: uploading, 2: minting, 3: registering

  const handleCandidateChange = (e) => {
    const { name, value } = e.target
    setCandidateForm((prev) => ({
      ...prev,
      [name]: value,
    }))
  }

  const handleFileChange = (file) => {
    setSelectedFile(file)
    // Clear the imageHash if file is removed
    if (!file) {
      setCandidateForm((prev) => ({
        ...prev,
        imageHash: "",
      }))
    }
  }

  const uploadToIPFS = async () => {
    if (!selectedFile) return null

    setIsUploading(true)
    setRegistrationStep(1) // Uploading

    try {
      const formData = new FormData()
      formData.append("file", selectedFile)

      toast.loading("Uploading image to IPFS...", {
        id: "upload-image",
      })

      const response = await fetch("/api/upload", {
        method: "POST",
        body: formData,
      })

      const data = await response.json()

      if (!response.ok) {
        throw new Error(data.error || "Failed to upload image")
      }

      // Set the IPFS hash in the form
      setCandidateForm((prev) => ({
        ...prev,
        imageHash: data.ipfsHash,
      }))

      toast.success("Image uploaded successfully!", {
        id: "upload-image",
      })
      return data.ipfsHash
    } catch (error) {
      console.error("Error uploading to IPFS:", error)
      toast.error(error.message || "Failed to upload image", {
        id: "upload-image",
      })
      return null
    } finally {
      setIsUploading(false)
    }
  }

  const handleVoterRegister = async (e) => {
    e.preventDefault()
    if (!contract || isSubmittingVoter) return

    try {
      setIsSubmittingVoter(true)

      // Call the contract's registerToVote function
      const tx = await contract.registerToVote(campaignId, voterPassKey)

      toast.loading("Minting your voting NFT...", {
        id: "register-voter",
      })
      setRegistrationStep(2) // Minting

      await tx.wait()

      toast.success("Successfully registered as a voter!", {
        id: "register-voter",
      })
      setRegistrationStep(3) // Registered

      // Clear form and refresh data
      setVoterPassKey("")
      onRegister()
    } catch (error) {
      console.error("Error registering as voter:", error)
      handleRpcError(error)
      setRegistrationStep(0) // Reset
    } finally {
      setIsSubmittingVoter(false)
    }
  }

  const handleCandidateRegister = async (e) => {
    e.preventDefault()
    if (!contract || isSubmittingCandidate) return

    try {
      setIsSubmittingCandidate(true)

      // Upload image to IPFS if selected
      let imageHash = candidateForm.imageHash
      if (selectedFile) {
        setRegistrationStep(1) // Uploading
        const uploadedHash = await uploadToIPFS()
        if (uploadedHash) {
          imageHash = uploadedHash
        }
      }

      // Call the contract's registerAsCandidate function
      setRegistrationStep(2) // Minting
      toast.loading("Minting your candidate NFT...", {
        id: "register-candidate",
      })

      const tx = await contract.registerAsCandidate(
        campaignId,
        candidateForm.name,
        candidateForm.statement,
        imageHash,
        candidateForm.passKey,
      )

      toast.loading("Registering as candidate...", {
        id: "register-candidate",
      })

      await tx.wait()

      toast.success("Successfully registered as a candidate!", {
        id: "register-candidate",
      })
      setRegistrationStep(3) // Registered

      // Clear form and refresh data
      setCandidateForm({
        name: "",
        statement: "",
        imageHash: "",
        passKey: "",
      })
      setSelectedFile(null)
      onRegister()
    } catch (error) {
      console.error("Error registering as candidate:", error)
      handleRpcError(error)
      setRegistrationStep(0) // Reset
    } finally {
      setIsSubmittingCandidate(false)
    }
  }

  // If user is already registered
  if (userIsRegistered) {
    return (
      <Card>
        <CardHeader>
          <CardTitle>Registration</CardTitle>
          <CardDescription>You are already registered for this campaign</CardDescription>
        </CardHeader>
        <CardContent>
          <Alert className="border-green-200 bg-green-50 dark:bg-green-950/20 dark:border-green-900">
            <Check className="h-4 w-4 text-green-600 dark:text-green-400" />
            <AlertDescription className="text-green-800 dark:text-green-300 ml-2">
              You have successfully registered for this campaign. You can participate when voting begins.
            </AlertDescription>
          </Alert>
        </CardContent>
      </Card>
    )
  }

  // Registration explanation for registration-required campaigns
  const renderRegistrationExplanation = () => (
    <Alert className="mb-6 border-blue-200 bg-blue-50 dark:bg-blue-950/20 dark:border-blue-900">
      <Info className="h-4 w-4 text-blue-600 dark:text-blue-400" />
      <AlertDescription className="text-blue-800 dark:text-blue-300 ml-2 text-sm">
        <p className="font-medium mb-1">About Registration NFTs</p>
        <p>
          When you register for voting, we mint an NFT for verification. This NFT has no financial value and loses its
          utility once you have voted. You can either keep it as proof of your participation, or burn it after voting.
        </p>
        <p className="mt-1 text-xs">Note: Burning the NFT requires gas fees as it is an on-chain transaction.</p>
      </AlertDescription>
    </Alert>
  )

  // Registration steps indicator
  const renderRegistrationSteps = () => {
    if (registrationStep === 0) return null

    const steps = [
      { label: "Uploading Image", icon: <Upload className="h-4 w-4" />, active: registrationStep === 1 },
      { label: "Minting NFT", icon: <Sparkles className="h-4 w-4" />, active: registrationStep === 2 },
      { label: "Registering", icon: <Check className="h-4 w-4" />, active: registrationStep === 3 },
    ]

    return (
      <div className="flex items-center justify-center space-x-2 mb-6">
        {steps.map((step, index) => (
          <div key={index} className="flex items-center">
            {index > 0 && (
              <div
                className={`h-0.5 w-8 ${index <= registrationStep ? "bg-purple-500" : "bg-gray-300 dark:bg-gray-700"}`}
              ></div>
            )}
            <div
              className={`flex items-center justify-center w-8 h-8 rounded-full ${
                index < registrationStep
                  ? "bg-green-100 text-green-600 dark:bg-green-900/30 dark:text-green-400"
                  : index === registrationStep
                    ? "bg-purple-100 text-purple-600 dark:bg-purple-900/30 dark:text-purple-400 animate-pulse"
                    : "bg-gray-100 text-gray-400 dark:bg-gray-800 dark:text-gray-500"
              }`}
            >
              {index < registrationStep ? <Check className="h-4 w-4" /> : step.icon}
            </div>
          </div>
        ))}
      </div>
    )
  }

  return (
    <Card>
      <CardHeader>
        <CardTitle>Register for Campaign</CardTitle>
        <CardDescription>Register to participate in this voting campaign</CardDescription>
      </CardHeader>
      <CardContent>
        {renderRegistrationExplanation()}
        {renderRegistrationSteps()}

        <Tabs defaultValue="voter" value={activeTab} onValueChange={setActiveTab}>
          <TabsList className="grid w-full grid-cols-2 mb-6">
            <TabsTrigger value="voter" className="flex items-center gap-2">
              <User className="h-4 w-4" />
              <span>Register as Voter</span>
            </TabsTrigger>
            {campaignInfo.votingType === 0 && (
              <TabsTrigger value="candidate" className="flex items-center gap-2">
                <UserPlus className="h-4 w-4" />
                <span>Register as Candidate</span>
              </TabsTrigger>
            )}
          </TabsList>

          <TabsContent value="voter">
            <form onSubmit={handleVoterRegister}>
              <div className="space-y-4">
                {campaignInfo.restriction === 2 && (
                  <div className="space-y-2">
                    <Label htmlFor="voterPassKey">Passkey</Label>
                    <Input
                      id="voterPassKey"
                      type="password"
                      placeholder="Enter the campaign passkey"
                      value={voterPassKey}
                      onChange={(e) => setVoterPassKey(e.target.value)}
                      required={campaignInfo.restriction === 2}
                    />
                    <p className="text-xs text-muted-foreground">
                      The passkey is required to verify your eligibility to vote in this campaign.
                    </p>
                  </div>
                )}

                <Button
                  type="submit"
                  className="w-full"
                  disabled={(campaignInfo.restriction === 2 && !voterPassKey) || isSubmittingVoter}
                >
                  {isSubmittingVoter ? "Registering..." : "Register as Voter"}
                </Button>
              </div>
            </form>
          </TabsContent>

          {campaignInfo.votingType === 0 && (
            <TabsContent value="candidate">
              <form onSubmit={handleCandidateRegister}>
                <div className="space-y-4">
                  <div className="space-y-2">
                    <Label htmlFor="name">Name</Label>
                    <Input
                      id="name"
                      name="name"
                      placeholder="Your candidate name"
                      value={candidateForm.name}
                      onChange={handleCandidateChange}
                      required
                    />
                  </div>

                  <div className="space-y-2">
                    <Label htmlFor="statement">Statement</Label>
                    <Textarea
                      id="statement"
                      name="statement"
                      placeholder="Share your platform or statement"
                      value={candidateForm.statement}
                      onChange={handleCandidateChange}
                      required
                      rows={3}
                    />
                  </div>

                  <div className="space-y-2">
                    <Label htmlFor="imageUpload">Profile Image</Label>
                    <FileUpload
                      onFileChange={handleFileChange}
                      value={
                        candidateForm.imageHash ? `https://gateway.pinata.cloud/ipfs/${candidateForm.imageHash}` : ""
                      }
                      isUploading={isUploading}
                    />
                    <p className="text-xs text-muted-foreground mt-1">
                      Upload a profile image to represent your candidacy (optional)
                    </p>
                  </div>

                  {campaignInfo.restriction === 2 && (
                    <div className="space-y-2">
                      <Label htmlFor="passKey">Passkey</Label>
                      <Input
                        id="passKey"
                        name="passKey"
                        type="password"
                        placeholder="Enter the campaign passkey"
                        value={candidateForm.passKey}
                        onChange={handleCandidateChange}
                        required={campaignInfo.restriction === 2}
                      />
                    </div>
                  )}

                  <Alert className="border-blue-200 bg-blue-50 dark:bg-blue-950/20 dark:border-blue-900">
                    <Info className="h-4 w-4 text-blue-600 dark:text-blue-400" />
                    <AlertDescription className="text-blue-800 dark:text-blue-300 ml-2 text-sm">
                      Registering as a candidate will allow others to vote for you in this campaign. Your wallet address
                      will be publicly associated with your candidacy.
                    </AlertDescription>
                  </Alert>

                  <Button
                    type="submit"
                    className="w-full"
                    disabled={
                      !candidateForm.name ||
                      !candidateForm.statement ||
                      (campaignInfo.restriction === 2 && !candidateForm.passKey) ||
                      isSubmittingCandidate ||
                      isUploading
                    }
                  >
                    {isSubmittingCandidate ? "Registering..." : "Register as Candidate"}
                  </Button>
                </div>
              </form>
            </TabsContent>
          )}
        </Tabs>
      </CardContent>
    </Card>
  )
}
