"use client"

import { Card, CardContent, CardDescription, CardHeader, CardTitle } from "@/components/ui/card"
import { Badge } from "@/components/ui/badge"
import { Trophy, Award, AlertCircle, Medal, Crown } from "lucide-react"
import { formatAddress, getActualVoterCount, getParticipationRate } from "@/lib/utils"
import {
  BarChart,
  Bar,
  XAxis,
  YAxis,
  CartesianGrid,
  Tooltip,
  ResponsiveContainer,
  PieChart,
  Pie,
  Cell,
  Legend,
} from "recharts"
import { motion } from "framer-motion"
import { Tabs, TabsContent, TabsList, TabsTrigger } from "@/components/ui/tabs"

export default function ResultsTab({ campaignInfo, proposals, candidates, isOwner = false }) {
  // Sort items by vote count (descending)
  const sortedProposals = [...proposals].sort((a, b) => b.voteCount - a.voteCount)
  const sortedCandidates = [...candidates].sort((a, b) => b.voteCount - a.voteCount)

  // Determine if there's a winner or tie
  const getWinnerStatus = () => {
    if (campaignInfo.votingType === 1 /* ProposalBased */) {
      if (proposals.length === 0) return { hasTie: false, hasWinner: false }

      const highestVotes = sortedProposals[0]?.voteCount || 0
      const tiedWinners = sortedProposals.filter((p) => p.voteCount === highestVotes)

      return {
        hasTie: tiedWinners.length > 1,
        hasWinner: highestVotes > 0,
        tiedWinners,
      }
    } else {
      if (candidates.length === 0) return { hasTie: false, hasWinner: false }

      const highestVotes = sortedCandidates[0]?.voteCount || 0
      const tiedWinners = sortedCandidates.filter((c) => c.voteCount === highestVotes)

      return {
        hasTie: tiedWinners.length > 1,
        hasWinner: highestVotes > 0,
        tiedWinners,
      }
    }
  }

  const winnerStatus = getWinnerStatus()

  // Prepare data for charts
  const prepareBarChartData = () => {
    if (campaignInfo.votingType === 1) {
      return proposals.map((p) => ({
        name: `Proposal ${p.id + 1}`,
        votes: p.voteCount,
      }))
    } else {
      return candidates.map((c) => ({
        name: c.name,
        votes: c.voteCount,
      }))
    }
  }

  const preparePieChartData = () => {
    if (campaignInfo.votingType === 1) {
      return proposals.map((p) => ({
        name: `Proposal ${p.id + 1}`,
        value: p.voteCount,
      }))
    } else {
      return candidates.map((c) => ({
        name: c.name,
        value: c.voteCount,
      }))
    }
  }

  const barChartData = prepareBarChartData()
  const pieChartData = preparePieChartData()

  // Colors for pie chart and rank medals
  const COLORS = [
    "#0088FE",
    "#00C49F",
    "#FFBB28",
    "#FF8042",
    "#8884d8",
    "#82ca9d",
    "#ffc658",
    "#8dd1e1",
    "#a4de6c",
    "#d0ed57",
  ]

  const MEDAL_COLORS = {
    0: "text-yellow-500", // Gold
    1: "text-slate-400", // Silver
    2: "text-amber-700", // Bronze
  }

  const MEDAL_BG_COLORS = {
    0: "bg-yellow-50 border-yellow-200 dark:bg-yellow-950/20 dark:border-yellow-900", // Gold
    1: "bg-slate-50 border-slate-200 dark:bg-slate-950/20 dark:border-slate-900", // Silver
    2: "bg-amber-50 border-amber-200 dark:bg-amber-950/20 dark:border-amber-900", // Bronze
  }

  const MEDAL_TEXT_COLORS = {
    0: "text-yellow-800 dark:text-yellow-300", // Gold
    1: "text-slate-800 dark:text-slate-300", // Silver
    2: "text-amber-800 dark:text-amber-300", // Bronze
  }

  // If no votes have been cast yet
  if (campaignInfo.totalVotes === 0) {
    return (
      <Card>
        <CardHeader>
          <CardTitle>Results</CardTitle>
          <CardDescription>No votes have been cast in this campaign yet.</CardDescription>
        </CardHeader>
        <CardContent className="text-center py-8">
          <AlertCircle className="h-16 w-16 mx-auto text-muted-foreground/50 mb-4" />
          <p className="text-muted-foreground">Once votes are cast, results will be displayed here.</p>
        </CardContent>
      </Card>
    )
  }

  // Render rank-based results (resultType === 0)
  const renderRankBasedResults = () => {
    return (
      <Card className="mb-6">
        <CardHeader>
          <CardTitle className="flex items-center gap-2">
            <Trophy className="h-5 w-5 text-yellow-500" />
            Ranked Results
          </CardTitle>
          <CardDescription>Options ranked by number of votes received</CardDescription>
        </CardHeader>
        <CardContent>
          <div className="space-y-4">
            {(campaignInfo.votingType === 1 ? sortedProposals : sortedCandidates).map((item, index) => {
              const isProposal = campaignInfo.votingType === 1
              const itemName = isProposal ? `Proposal ${item.id + 1}` : item.name
              const itemContent = isProposal ? item.content : item.statement
              const itemAddress = !isProposal ? item.address : null
              const percentage = Math.round((item.voteCount / campaignInfo.totalVotes) * 100)

              // Determine if this is a medal position (top 3)
              const isMedalPosition = index < 3

              return (
                <motion.div
                  key={isProposal ? item.id : item.address}
                  initial={{ opacity: 0, y: 20 }}
                  animate={{ opacity: 1, y: 0 }}
                  transition={{ delay: index * 0.1, duration: 0.3 }}
                  className={`border rounded-lg p-4 ${isMedalPosition ? MEDAL_BG_COLORS[index] : "bg-background/60"}`}
                >
                  <div className="flex items-center justify-between mb-2">
                    <div className="flex items-center gap-2">
                      {isMedalPosition ? (
                        index === 0 ? (
                          <Crown className={`h-5 w-5 ${MEDAL_COLORS[index]}`} />
                        ) : index === 1 ? (
                          <Medal className={`h-5 w-5 ${MEDAL_COLORS[index]}`} />
                        ) : (
                          <Award className={`h-5 w-5 ${MEDAL_COLORS[index]}`} />
                        )
                      ) : (
                        <div className="flex items-center justify-center w-5 h-5 rounded-full bg-muted text-xs font-medium">
                          {index + 1}
                        </div>
                      )}
                      <h3 className={`font-medium ${isMedalPosition ? MEDAL_TEXT_COLORS[index] : ""}`}>{itemName}</h3>
                    </div>
                    <div className="flex items-center gap-2">
                      <span className="text-sm text-muted-foreground">{percentage}%</span>
                      <Badge variant={isMedalPosition ? "outline" : "secondary"}>
                        {item.voteCount} {item.voteCount === 1 ? "vote" : "votes"}
                      </Badge>
                    </div>
                  </div>

                  {itemAddress && <p className="text-xs text-muted-foreground mb-1">{formatAddress(itemAddress)}</p>}

                  <p className={`text-sm ${isMedalPosition ? MEDAL_TEXT_COLORS[index] : "text-muted-foreground"}`}>
                    {itemContent}
                  </p>

                  <div className="mt-2 w-full bg-muted rounded-full h-2.5">
                    <div
                      className={`h-2.5 rounded-full ${isMedalPosition ? "bg-primary/90" : "bg-primary/70"}`}
                      style={{ width: `${percentage}%` }}
                    ></div>
                  </div>
                </motion.div>
              )
            })}
          </div>
        </CardContent>
      </Card>
    )
  }

  // Render single-winner results (resultType === 1)
  const renderSingleWinnerResults = () => {
    if (!winnerStatus.hasWinner) return null

    const isProposal = campaignInfo.votingType === 1
    const winner = isProposal ? sortedProposals[0] : sortedCandidates[0]
    const winnerName = isProposal ? `Proposal ${winner.id + 1}` : winner.name
    const winnerContent = isProposal ? winner.content : winner.statement
    const winnerAddress = !isProposal ? winner.address : null
    const percentage = Math.round((winner.voteCount / campaignInfo.totalVotes) * 100)

    // Check if there's a tie
    const isTie = winnerStatus.hasTie
    const tiedWinners = winnerStatus.tiedWinners

    return (
      <Card className="mb-6 overflow-hidden">
        <div className="bg-gradient-to-r from-violet-500 to-indigo-600 text-white p-4">
          <div className="flex items-center gap-2">
            <Crown className="h-6 w-6 text-yellow-300" />
            <h2 className="text-xl font-bold">{isTie ? "Tie Detected" : "Winner"}</h2>
          </div>
          <p className="text-white/80 text-sm">
            {isTie
              ? `${tiedWinners.length} options received the same number of votes`
              : "The winning option based on vote count"}
          </p>
        </div>

        <CardContent className="pt-6">
          {isTie ? (
            <div className="space-y-4">
              {tiedWinners.map((item, index) => {
                const itemName = isProposal ? `Proposal ${item.id + 1}` : item.name
                const itemContent = isProposal ? item.content : item.statement
                const itemAddress = !isProposal ? item.address : null

                return (
                  <motion.div
                    key={isProposal ? item.id : item.address}
                    initial={{ opacity: 0, scale: 0.95 }}
                    animate={{ opacity: 1, scale: 1 }}
                    transition={{ delay: index * 0.1, duration: 0.3 }}
                    className="bg-amber-50 dark:bg-amber-950/20 border border-amber-200 dark:border-amber-900 rounded-lg p-4"
                  >
                    <div className="flex justify-between items-center">
                      <div className="flex items-center gap-2">
                        <Trophy className="h-5 w-5 text-amber-500" />
                        <h3 className="font-medium text-amber-800 dark:text-amber-300">{itemName}</h3>
                      </div>
                      <Badge>{item.voteCount} votes</Badge>
                    </div>

                    {itemAddress && (
                      <p className="text-xs text-amber-600 dark:text-amber-400 mt-1">{formatAddress(itemAddress)}</p>
                    )}

                    <p className="text-sm mt-2 text-amber-700 dark:text-amber-300">{itemContent}</p>
                  </motion.div>
                )
              })}
            </div>
          ) : (
            <motion.div
              initial={{ opacity: 0, y: 20 }}
              animate={{ opacity: 1, y: 0 }}
              transition={{ duration: 0.5 }}
              className="text-center"
            >
              <div className="inline-flex items-center justify-center w-24 h-24 rounded-full bg-green-100 dark:bg-green-900/30 mb-4">
                <Trophy className="h-12 w-12 text-green-600 dark:text-green-400" />
              </div>

              <h3 className="text-2xl font-bold text-green-800 dark:text-green-300 mb-2">{winnerName}</h3>

              {winnerAddress && (
                <p className="text-sm text-green-600 dark:text-green-400 mb-2">{formatAddress(winnerAddress)}</p>
              )}

              <div className="max-w-md mx-auto mb-4">
                <p className="text-green-700 dark:text-green-300">{winnerContent}</p>
              </div>

              <div className="flex items-center justify-center gap-4 mt-6">
                <div className="text-center">
                  <div className="text-3xl font-bold text-green-600 dark:text-green-400">{winner.voteCount}</div>
                  <div className="text-sm text-muted-foreground">Votes</div>
                </div>

                <div className="h-10 border-r border-muted"></div>

                <div className="text-center">
                  <div className="text-3xl font-bold text-green-600 dark:text-green-400">{percentage}%</div>
                  <div className="text-sm text-muted-foreground">Of Total</div>
                </div>
              </div>

              <div className="mt-6 w-full max-w-md mx-auto bg-muted rounded-full h-4">
                <div className="bg-green-500 h-4 rounded-full" style={{ width: `${percentage}%` }}></div>
              </div>
            </motion.div>
          )}
        </CardContent>
      </Card>
    )
  }

  return (
    <div className="space-y-6">
      {/* Conditional rendering based on resultType */}
      {campaignInfo.resultType === 0
        ? // Rank-based results
          renderRankBasedResults()
        : // Single-winner results
          renderSingleWinnerResults()}

      {/* Visualization Tabs */}
      <Tabs defaultValue="bar" className="w-full">
        <CardHeader className="px-0 pt-0">
          <div className="flex flex-col sm:flex-row sm:justify-between sm:items-center gap-4">
            <div>
              <CardTitle>Results Visualization</CardTitle>
              <CardDescription>View the results in different formats</CardDescription>
            </div>
            <TabsList>
              <TabsTrigger value="bar">Bar Chart</TabsTrigger>
              <TabsTrigger value="pie">Pie Chart</TabsTrigger>
              <TabsTrigger value="table">Table</TabsTrigger>
            </TabsList>
          </div>
        </CardHeader>

        <TabsContent value="bar">
          <Card>
            <CardContent className="h-[400px] pt-6">
              <ResponsiveContainer width="100%" height="100%">
                <BarChart
                  data={barChartData}
                  margin={{
                    top: 5,
                    right: 30,
                    left: 20,
                    bottom: 60,
                  }}
                >
                  <CartesianGrid strokeDasharray="3 3" />
                  <XAxis dataKey="name" angle={-45} textAnchor="end" height={70} interval={0} />
                  <YAxis />
                  <Tooltip />
                  <Bar dataKey="votes" fill="#8884d8" />
                </BarChart>
              </ResponsiveContainer>
            </CardContent>
          </Card>
        </TabsContent>

        <TabsContent value="pie">
          <Card>
            <CardContent className="h-[400px] pt-6">
              <ResponsiveContainer width="100%" height="100%">
                <PieChart>
                  <Pie
                    data={pieChartData}
                    cx="50%"
                    cy="50%"
                    labelLine={false}
                    label={({ name, percent }) => `${name}: ${(percent * 100).toFixed(0)}%`}
                    outerRadius={80}
                    fill="#8884d8"
                    dataKey="value"
                  >
                    {pieChartData.map((entry, index) => (
                      <Cell key={`cell-${index}`} fill={COLORS[index % COLORS.length]} />
                    ))}
                  </Pie>
                  <Tooltip formatter={(value) => [`${value} votes`, "Votes"]} />
                  <Legend />
                </PieChart>
              </ResponsiveContainer>
            </CardContent>
          </Card>
        </TabsContent>

        <TabsContent value="table">
          <Card>
            <CardContent className="pt-6">
              <div className="space-y-4">
                {campaignInfo.votingType === 1 /* ProposalBased */
                  ? // Proposal results
                    sortedProposals.map((proposal, index) => (
                      <div key={proposal.id} className="bg-background/60 p-4 rounded-lg">
                        <div className="flex justify-between items-center mb-2">
                          <div className="flex items-center">
                            {index === 0 && !winnerStatus.hasTie && proposal.voteCount > 0 && (
                              <Trophy className="h-4 w-4 text-yellow-500 mr-2" />
                            )}
                            <h3 className="font-medium">Proposal {proposal.id + 1}</h3>
                          </div>
                          <Badge variant="secondary">{proposal.voteCount} votes</Badge>
                        </div>
                        <p className="text-sm text-muted-foreground">{proposal.content}</p>
                        <div className="mt-2 w-full bg-muted rounded-full h-2.5">
                          <div
                            className="bg-primary h-2.5 rounded-full"
                            style={{
                              width: `${
                                campaignInfo.totalVotes > 0 ? (proposal.voteCount / campaignInfo.totalVotes) * 100 : 0
                              }%`,
                            }}
                          ></div>
                        </div>
                        <p className="text-xs text-right mt-1 text-muted-foreground">
                          {campaignInfo.totalVotes > 0
                            ? Math.round((proposal.voteCount / campaignInfo.totalVotes) * 100)
                            : 0}
                          %
                        </p>
                      </div>
                    ))
                  : // Candidate results
                    sortedCandidates.map((candidate, index) => (
                      <div key={candidate.id} className="bg-background/60 p-4 rounded-lg">
                        <div className="flex justify-between items-center mb-2">
                          <div className="flex items-center">
                            {index === 0 && !winnerStatus.hasTie && candidate.voteCount > 0 && (
                              <Trophy className="h-4 w-4 text-yellow-500 mr-2" />
                            )}
                            <h3 className="font-medium">{candidate.name}</h3>
                          </div>
                          <Badge variant="secondary">{candidate.voteCount} votes</Badge>
                        </div>
                        <p className="text-xs text-muted-foreground">{formatAddress(candidate.address)}</p>
                        <div className="mt-2 w-full bg-muted rounded-full h-2.5">
                          <div
                            className="bg-primary h-2.5 rounded-full"
                            style={{
                              width: `${
                                campaignInfo.totalVotes > 0 ? (candidate.voteCount / campaignInfo.totalVotes) * 100 : 0
                              }%`,
                            }}
                          ></div>
                        </div>
                        <p className="text-xs text-right mt-1 text-muted-foreground">
                          {campaignInfo.totalVotes > 0
                            ? Math.round((candidate.voteCount / campaignInfo.totalVotes) * 100)
                            : 0}
                          %
                        </p>
                      </div>
                    ))}
              </div>
            </CardContent>
          </Card>
        </TabsContent>
      </Tabs>

      {/* Summary Card */}
      <Card>
        <CardHeader>
          <CardTitle>Voting Summary</CardTitle>
          <CardDescription>Overview of participation and voting statistics</CardDescription>
        </CardHeader>
        <CardContent>
          <div className="grid grid-cols-1 md:grid-cols-3 gap-4">
            <div className="bg-background/60 p-4 rounded-lg text-center">
              <h3 className="text-sm text-muted-foreground mb-1">Total Votes</h3>
              <p className="text-3xl font-bold">{campaignInfo.totalVotes}</p>
            </div>
            <div className="bg-background/60 p-4 rounded-lg text-center">
              <h3 className="text-sm text-muted-foreground mb-1">Registered Voters</h3>
              <p className="text-3xl font-bold">{getActualVoterCount(campaignInfo)}</p>
            </div>
            <div className="bg-background/60 p-4 rounded-lg text-center">
              <h3 className="text-sm text-muted-foreground mb-1">Participation Rate</h3>
              <p className="text-3xl font-bold">
                {campaignInfo.registeredVoterCount > 0 ? getParticipationRate(campaignInfo) : 0}%
              </p>
            </div>
          </div>
        </CardContent>
      </Card>
    </div>
  )
}
