"use client"

import { Card, CardContent, CardDescription, CardHeader, CardTitle } from "@/components/ui/card"
import { Badge } from "@/components/ui/badge"
import { Alert, AlertDescription } from "@/components/ui/alert"
import { Info, Users, Award, BarChart2, Calendar, Clock, Shield } from "lucide-react"
import {
  formatAddress,
  formatDate,
  getVotingTypeText,
  getRestrictionText,
  getResultTypeText,
  resolveIpfsUrl,
  getActualVoterCount,
  getParticipationRate,
} from "@/lib/utils"

export default function OverviewTab({
  campaignInfo,
  isOwner,
  isBeforeStart,
  isDuringVoting,
  isAfterEnd,
  proposals,
  candidates,
}) {
  // Determine if we should show voter registration stats
  const showVoterStats = campaignInfo.restriction !== 0 || campaignInfo.votingType === 1

  // Get actual voter count (excluding candidates in candidate-based voting)
  const actualVoterCount = getActualVoterCount(campaignInfo)

  // Calculate participation rate
  const participationRate = getParticipationRate(campaignInfo)

  return (
    <div className="space-y-6">
      {/* Campaign Details */}
      <div className="grid grid-cols-1 lg:grid-cols-3 gap-6">
        <Card className="lg:col-span-2 border-purple-900/20 bg-gradient-to-r from-gray-50 to-purple-50 dark:from-gray-950 dark:to-purple-950/20">
          <CardHeader>
            <CardTitle className="flex items-center gap-2 text-gray-900 dark:text-gray-100">
              <Info className="h-5 w-5 text-purple-800 dark:text-purple-400" />
              Campaign Details
            </CardTitle>
            <CardDescription className="text-gray-700 dark:text-gray-300">
              Information about this voting campaign
            </CardDescription>
          </CardHeader>
          <CardContent className="space-y-6">
            <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
              <div className="space-y-4">
                <div>
                  <h3 className="text-sm font-medium text-gray-600 dark:text-gray-400 mb-1">Description</h3>
                  <p className="font-medium text-gray-900 dark:text-gray-100">{campaignInfo.description}</p>
                </div>

                <div>
                  <h3 className="text-sm font-medium text-gray-600 dark:text-gray-400 mb-1">Creator</h3>
                  <p className="font-medium text-gray-900 dark:text-gray-100">
                    {formatAddress(campaignInfo.creator)}
                    {isOwner && <span className="ml-2 text-purple-700 dark:text-purple-400">(You)</span>}
                  </p>
                </div>

                <div>
                  <h3 className="text-sm font-medium text-gray-600 dark:text-gray-400 mb-1">Campaign Type</h3>
                  <div className="flex flex-wrap gap-2">
                    <Badge
                      variant="outline"
                      className="bg-white dark:bg-gray-900 text-gray-900 dark:text-gray-100 border-purple-200 dark:border-purple-800"
                    >
                      {getVotingTypeText(campaignInfo.votingType)}
                    </Badge>
                    <Badge
                      variant="outline"
                      className="bg-white dark:bg-gray-900 text-gray-900 dark:text-gray-100 border-purple-200 dark:border-purple-800"
                    >
                      {getRestrictionText(campaignInfo.restriction)}
                    </Badge>
                    <Badge
                      variant="outline"
                      className="bg-white dark:bg-gray-900 text-gray-900 dark:text-gray-100 border-purple-200 dark:border-purple-800"
                    >
                      Results: {getResultTypeText(campaignInfo.resultType)}
                    </Badge>
                  </div>
                </div>
              </div>

              <div className="space-y-4">
                <div>
                  <h3 className="text-sm font-medium text-gray-600 dark:text-gray-400 mb-1">Timeline</h3>
                  <div className="space-y-2">
                    <div className="flex items-center gap-2">
                      <Calendar className="h-4 w-4 text-purple-700 dark:text-purple-400" />
                      <span className="text-sm text-gray-900 dark:text-gray-100">
                        Start: {formatDate(campaignInfo.startTime * 1000)}
                      </span>
                    </div>
                    <div className="flex items-center gap-2">
                      <Calendar className="h-4 w-4 text-purple-700 dark:text-purple-400" />
                      <span className="text-sm text-gray-900 dark:text-gray-100">
                        End: {formatDate(campaignInfo.endTime * 1000)}
                      </span>
                    </div>
                  </div>
                </div>

                <div>
                  <h3 className="text-sm font-medium text-gray-600 dark:text-gray-400 mb-1">Current Phase</h3>
                  <div className="flex items-center gap-2">
                    <Clock className="h-4 w-4 text-purple-700 dark:text-purple-400" />
                    <span className="text-sm text-gray-900 dark:text-gray-100">
                      {isBeforeStart ? "Registration Phase" : isDuringVoting ? "Voting Phase" : "Ended"}
                    </span>
                  </div>
                </div>

                <div>
                  <h3 className="text-sm font-medium text-gray-600 dark:text-gray-400 mb-1">Access Control</h3>
                  <div className="flex items-center gap-2">
                    <Shield className="h-4 w-4 text-purple-700 dark:text-purple-400" />
                    <span className="text-sm text-gray-900 dark:text-gray-100">
                      {campaignInfo.restriction === 0
                        ? "Open to all wallet addresses"
                        : campaignInfo.restriction === 1
                          ? `Limited to ${campaignInfo.maxVoters} voters`
                          : "Registration with passkey required"}
                    </span>
                  </div>
                </div>
              </div>
            </div>

            {/* Phase-specific information */}
            {isBeforeStart && (
              <Alert className="border-amber-200 bg-amber-50 dark:bg-amber-950/20 dark:border-amber-900">
                <Info className="h-4 w-4 text-amber-600 dark:text-amber-400" />
                <AlertDescription className="text-amber-800 dark:text-amber-300 ml-2">
                  This campaign is in the registration phase. Voting will begin at the scheduled start time.
                </AlertDescription>
              </Alert>
            )}

            {isDuringVoting && (
              <Alert className="border-purple-200 bg-purple-50 dark:bg-purple-950/20 dark:border-purple-900">
                <Info className="h-4 w-4 text-purple-600 dark:text-purple-400" />
                <AlertDescription className="text-purple-800 dark:text-purple-300 ml-2">
                  Voting is currently active. Cast your vote before the campaign ends.
                </AlertDescription>
              </Alert>
            )}

            {isAfterEnd && (
              <Alert className="border-gray-200 bg-gray-50 dark:bg-gray-950/20 dark:border-gray-900">
                <Info className="h-4 w-4 text-gray-600 dark:text-gray-400" />
                <AlertDescription className="text-gray-800 dark:text-gray-300 ml-2">
                  This campaign has ended. Results are now available.
                </AlertDescription>
              </Alert>
            )}
          </CardContent>
        </Card>

        {/* Stats Card */}
        <Card className="border-purple-900/20 bg-gradient-to-r from-gray-50 to-purple-50 dark:from-gray-950 dark:to-purple-950/20">
          <CardHeader>
            <CardTitle className="flex items-center gap-2 text-gray-900 dark:text-gray-100">
              <BarChart2 className="h-5 w-5 text-purple-800 dark:text-purple-400" />
              Campaign Stats
            </CardTitle>
            <CardDescription className="text-gray-700 dark:text-gray-300">
              Current statistics for this campaign
            </CardDescription>
          </CardHeader>
          <CardContent className="space-y-6">
            <div className="grid grid-cols-1 gap-4">
              {showVoterStats && (
                <div className="bg-white dark:bg-gray-900 p-4 rounded-lg flex flex-col items-center justify-center text-center shadow-sm">
                  <Users className="h-8 w-8 text-purple-700 dark:text-purple-400 mb-2" />
                  <h3 className="text-lg font-medium text-gray-900 dark:text-gray-100">Registered Voters</h3>
                  <p className="text-3xl font-bold text-gray-900 dark:text-gray-100">{actualVoterCount}</p>
                </div>
              )}

              <div className="bg-white dark:bg-gray-900 p-4 rounded-lg flex flex-col items-center justify-center text-center shadow-sm">
                <Award className="h-8 w-8 text-purple-700 dark:text-purple-400 mb-2" />
                <h3 className="text-lg font-medium text-gray-900 dark:text-gray-100">Total Votes</h3>
                <p className="text-3xl font-bold text-gray-900 dark:text-gray-100">{campaignInfo.totalVotes}</p>
              </div>

              <div className="bg-white dark:bg-gray-900 p-4 rounded-lg flex flex-col items-center justify-center text-center shadow-sm">
                <BarChart2 className="h-8 w-8 text-purple-700 dark:text-purple-400 mb-2" />
                <h3 className="text-lg font-medium text-gray-900 dark:text-gray-100">
                  {campaignInfo.votingType === 0 ? "Candidates" : "Proposals"}
                </h3>
                <p className="text-3xl font-bold text-gray-900 dark:text-gray-100">{campaignInfo.itemCount}</p>
              </div>
            </div>

            {showVoterStats && actualVoterCount > 0 && (
              <div className="bg-white dark:bg-gray-900 p-4 rounded-lg shadow-sm">
                <h3 className="font-medium mb-2 text-gray-900 dark:text-gray-100">Participation Rate</h3>
                <div className="w-full bg-gray-200 dark:bg-gray-700 rounded-full h-2.5">
                  <div
                    className="bg-purple-700 dark:bg-purple-500 h-2.5 rounded-full"
                    style={{
                      width: `${participationRate}%`,
                    }}
                  ></div>
                </div>
                <p className="text-xs text-right mt-1 text-gray-600 dark:text-gray-400">{participationRate}%</p>
              </div>
            )}
          </CardContent>
        </Card>
      </div>

      {/* Items Preview */}
      {(proposals.length > 0 || candidates.length > 0) && (
        <Card className="border-purple-900/20 bg-gradient-to-r from-gray-50 to-purple-50 dark:from-gray-950 dark:to-purple-950/20">
          <CardHeader>
            <CardTitle className="text-gray-900 dark:text-gray-100">
              {campaignInfo.votingType === 0 ? "Candidates" : "Proposals"} Preview
            </CardTitle>
            <CardDescription className="text-gray-700 dark:text-gray-300">
              {campaignInfo.votingType === 0
                ? "Registered candidates for this campaign"
                : "Available proposals for voting"}
            </CardDescription>
          </CardHeader>
          <CardContent>
            <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
              {campaignInfo.votingType === 1 /* ProposalBased */
                ? proposals.slice(0, 4).map((proposal) => (
                    <div key={proposal.id} className="bg-white dark:bg-gray-900 p-4 rounded-lg shadow-sm">
                      <h3 className="font-medium mb-1 text-gray-900 dark:text-gray-100">Proposal {proposal.id + 1}</h3>
                      <p className="text-sm text-gray-600 dark:text-gray-400 line-clamp-2">{proposal.content}</p>
                    </div>
                  ))
                : candidates.slice(0, 4).map((candidate) => (
                    <div key={candidate.id} className="bg-white dark:bg-gray-900 p-4 rounded-lg flex shadow-sm">
                      {/* Added candidate image */}
                      <div className="mr-3 flex-shrink-0">
                        {candidate.imageHash ? (
                          <img
                            src={resolveIpfsUrl(candidate.imageHash) || "/placeholder.svg"}
                            alt={`${candidate.name}'s profile`}
                            className="w-16 h-16 rounded-full object-cover border-2 border-purple-200 dark:border-purple-800"
                            onError={(e) => {
                              e.target.onerror = null
                              e.target.src = "/placeholder-profile.png"
                            }}
                          />
                        ) : (
                          <div className="w-16 h-16 rounded-full bg-gray-200 dark:bg-gray-700 flex items-center justify-center border-2 border-purple-200 dark:border-purple-800">
                            <Users className="h-6 w-6 text-gray-500 dark:text-gray-400" />
                          </div>
                        )}
                      </div>
                      <div className="flex-grow">
                        <h3 className="font-medium mb-1 text-gray-900 dark:text-gray-100">{candidate.name}</h3>
                        <p className="text-xs text-gray-500 dark:text-gray-500 mb-1">
                          {formatAddress(candidate.address)}
                        </p>
                        <p className="text-sm text-gray-600 dark:text-gray-400 line-clamp-2">{candidate.statement}</p>
                      </div>
                    </div>
                  ))}
            </div>
            {((campaignInfo.votingType === 1 && proposals.length > 4) ||
              (campaignInfo.votingType === 0 && candidates.length > 4)) && (
              <p className="text-center text-sm text-gray-600 dark:text-gray-400 mt-4">
                And {campaignInfo.votingType === 1 ? proposals.length - 4 : candidates.length - 4} more...
              </p>
            )}
          </CardContent>
        </Card>
      )}
    </div>
  )
}
