"use client"

import { useEffect } from "react"

import { useState } from "react"

import { useContract } from "@/context/contract-context"
import { CheckCircle, AlertCircle } from "lucide-react"

const UserStatusIndicator = ({ campaignInfo }) => {
  const { isConnected, address } = useContract()

  // Check if user has voted or is registered based on contract data
  const [userHasVoted, userIsRegistered] = useUserStatus(campaignInfo)

  if (!isConnected) {
    return (
      <div className="bg-purple-800/50 border border-purple-700 rounded-md p-3 flex items-center gap-3">
        <AlertCircle className="h-5 w-5 text-purple-300" />
        <p className="text-purple-200">Connect your wallet to participate in this campaign</p>
      </div>
    )
  }

  if (userHasVoted) {
    return (
      <div className="bg-green-900/30 border border-green-800 rounded-md p-3 flex items-center gap-3">
        <CheckCircle className="h-5 w-5 text-green-400" />
        <p className="text-green-200">✅ Your vote has been cast. Thank you for participating!</p>
      </div>
    )
  }

  if (campaignInfo.restriction !== 0 && userIsRegistered) {
    return (
      <div className="bg-blue-900/30 border border-blue-800 rounded-md p-3 flex items-center gap-3">
        <CheckCircle className="h-5 w-5 text-blue-400" />
        <p className="text-blue-200">✅ You are registered to vote in this campaign</p>
      </div>
    )
  }

  if (campaignInfo.restriction !== 0 && !userIsRegistered) {
    return (
      <div className="bg-yellow-900/30 border border-yellow-800 rounded-md p-3 flex items-center gap-3">
        <AlertCircle className="h-5 w-5 text-yellow-400" />
        <p className="text-yellow-200">You need to register before you can vote in this campaign</p>
      </div>
    )
  }

  return null
}

// Custom hook to get user status
function useUserStatus(campaignInfo) {
  const { contract, address } = useContract()
  const [userHasVoted, setUserHasVoted] = useState(false)
  const [userIsRegistered, setUserIsRegistered] = useState(false)

  useEffect(() => {
    const checkUserStatus = async () => {
      if (!contract || !address || !campaignInfo) return

      try {
        const [voted, registered] = await Promise.all([
          contract.hasVoted(campaignInfo.id, address),
          contract.isRegisteredVoter(campaignInfo.id, address),
        ])
        setUserHasVoted(voted)
        setUserIsRegistered(registered)
      } catch (err) {
        console.error("Error checking user status:", err)
      }
    }

    checkUserStatus()
  }, [contract, address, campaignInfo])

  return [userHasVoted, userIsRegistered]
}

export default UserStatusIndicator

